<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="dialogStudent = true">EXCEL</el-button>
		</div>

		<!-- Excel -->
		<el-dialog title="" :visible.sync="dialogStudent" width="500px" :close-on-click-modal="false">
			<div class="btn">
				<el-select v-model="grade" placeholder="" @change="gradeOnChange">
					<el-option v-for="item in gradeList" :key="item.id" :label="item.title" :value="item.id">
					</el-option>
				</el-select>
			</div>
			<div class="btn">
			<el-select v-model="class_id" placeholder="">
				<el-option v-for="item in classList" :key="item.id" :label="item.title" :value="item.id">
				</el-option>
			</el-select>
			</div>
			<div class="btn">
				<el-button type="primary" icon="el-icon-download"></el-button>
			</div>
			<el-upload class="upload-demo" ref="upload" drag action="https://school.fengniaotuangou.cn/import/students" :data="classData"
			 :on-remove="handleRemove" :file-list="fileList" :auto-upload="false" :on-success="handleSuccess" :headers="uploadHeader">
				<i class="el-icon-upload"></i>
				<div class="el-upload__text"><em></em></div>
				<div slot="tip" class="el-upload__tip">EXCEL</div>
			</el-upload>
			<div class="upload-btn">
				<el-button type="primary" @click="upload"></el-button>
			</div>
		</el-dialog>

		<el-table :data="tableDate" ref="multipleTable" class="aip-table">
			<el-table-column label="" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="ID" align="center"></el-table-column>
			<el-table-column prop="name" label="" align="center"></el-table-column>
			<el-table-column prop="remark" label="" align="center"></el-table-column>
			<el-table-column label="" align="center">
				<template slot-scope="scope">
					<el-button size="mini" @click="handleDel(scope.$index,scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import API from '@/api/index.js'
	
	export default {
		name: 'studentadd',
		data() {
			return {
				dialogStudent: false,
				grade: '',
				gradeList: [],
				class_id: '',
				classList: [],
				fileList: [],
				classData: {
					class_id: ''  // 
				},
				
				tableDate: [],
				currentPage: 1,
				totalPage: 0
			}
		},
		computed: {
			uploadHeader() {
				return {
					'token': localStorage.getItem('token')
				}
			}
		},
		mounted() {
			this.getGrade();
			this.getClass()
		},
		methods: {
			// 
			getGrade() {
				var self = this;
				API.grades(self.currentPage).then(res => {
					self.gradeList = res.data;
				})
			},
			getClass(grade_id) {
				var self = this;
				// API.classes1(grade_id,30).then(res => {
				// 	self.classList = res.data;
				// })
				API.classes(1, 1000, grade_id).then(res => {
					self.classList = res.data;
				})
			},
			// 
			gradeOnChange(value) {
				var self = this;
				self.grade = value;
				self.getClass(value)
			},
			// 
			handleDel() {},
			// 
			upload() {
				this.classData.class_id = this.class_id
				this.$refs.upload.submit();
			},
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handleSuccess(file, fileList) {
				this.dialogStudent = false;
				this.$message.success('');
				this.fileList = []
			},
			// 
			handleCurrentChange() {}
		}
	}
</script>


<style scoped>
	.upload-btn {
		margin-top: 10px;
	}
</style>
